.button {
  position: absolute;
  top: 30px;
  left: 30px;
  background: yellowgreen;
  border: 1px solid green;
  padding: 10px 20px;
  border-radius: 7px;
  font-size: 20px;
  font-weight: bold;
}

@media (max-width: 767px) {
  .button {
    font-size: 18px;
    transform: rotate(-90deg);
    top: 60px;
    left: 0px;
  }
}

@media (max-width: 600px) {
  .button {
    font-size: 16px;
    left: -5px;
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  .button {
    font-size: 14px;
    left: -5px;
    top: 55px;
    padding: 8px 15px;
  }
}

/* Iphone 5 */
@media (max-width: 320px) {
  .button {
    left: -15px;
    top: 35px;
    padding: 5px 10px;
  }
}
