.container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
}

.plate {
  width: 530px;
  height: 310px;
  background-image: url("./../../assets/img/backgroundWin.png");
  background-size: cover;
  padding: 20px;
  border-radius: 15px;
}

@media (max-width: 600px) {
  .plate {
    width: 430px;
    height: 250px;
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .plate {
    width: 360px;
    height: 210px;
    padding: 10px;
  }
}

@media (max-width: 400px) {
  .plate {
    width: 310px;
    height: 182px;
  }
}

@media (max-width: 320px) {
  .plate {
    width: 290px;
    height: 170px;
  }
}
