.container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 500px;
  height: 500px;
  outline: 3px solid #982400;
  overflow: hidden;
  box-shadow: 0px 0px 100px 30px black;
}

@media (max-width: 600px) {
  .container {
    width: 400px;
    height: 400px;
    box-shadow: 0px 0px 90px 25px black;
  }
}

@media (max-width: 480px) {
  .container {
    width: 340px;
    height: 340px;
		box-shadow: 0px 0px 80px 20px black;
  }
}

@media (max-width: 400px) {
  .container {
    width: 280px;
    height: 280px;
    box-shadow: 0px 0px 70px 15px black;
  }
}

@media (max-width: 320px) {
  .container {
    width: 260px;
    height: 260px;
    box-shadow: 0px 0px 50px 10px black;
  }
}
