.container {
  display: flex;
  position: relative;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 406px;
  background-image: url("./../../assets/img/fieldBackground.jpg");
  border: 3px solid #982400;
  background-size: 100%;
}

@media (max-width: 600px) {
  .container {
    width: 330px;
    height: 330px;
  }
}

@media (max-width: 480px) {
  .container {
    width: 290px;
    height: 290px;
  }
}

@media (max-width: 400px) {
  .container {
    width: 240px;
    height: 240px;
  }
}

@media (max-width: 320px) {
  .container {
    width: 220px;
    height: 220px;
  }
}
