.container {
  position: absolute;
  bottom: 40px;
  display: flex;
  justify-content: center;
  border-radius: 15px;
  width: 100%;
}

.button {
  border-radius: 15px;
  padding: 15px 55px;
  background: #03a9f4;
  font-size: 30px;
  font-weight: bold;
  color: black;
}

@media (max-width: 600px) {
  .button {
    font-size: 24px;
  }
  .container {
    bottom: 35px;
  }
}

@media (max-width: 480px) {
  .button {
    padding: 10px 40px;
    font-size: 20px;
  }
  .container {
    bottom: 25px;
  }
}

@media (max-width: 320px) {
  .button {
    padding: 6px 28px;
    font-size: 16px;
  }
  .container {
    bottom: 14px;
  }
}
