.textWin {
  text-align: center;
  font-size: 70px;
  font-weight: bold;
  color: #b11919;
  text-shadow: 0px 0px 12px black;
}
.textTimer {
  text-align: center;
  font-size: 34px;
  text-shadow: 0px 0px 7px black;
}
.textAgain {
  text-align: center;
  font-size: 46px;
  color: #5a1f1f;
  text-shadow: 0px 0px 10px black;
  margin-bottom: 15px;
}
.buttons {
  display: flex;
  justify-content: center;
}

@media (max-width: 600px) {
  .textWin {
    font-size: 56px;
  }
  .textTimer {
    text-align: center;
    font-size: 26px;
  }
  .textAgain {
    font-size: 36px;
    margin-bottom: 14px;
  }
}

@media (max-width: 480px) {
  .textWin {
    font-size: 48px;
  }
  .textTimer {
    text-align: center;
    font-size: 22px;
  }
  .textAgain {
    font-size: 30px;
  }
}

@media (max-width: 400px) {
  .textWin {
    font-size: 44px;
  }
  .textTimer {
    text-align: center;
    font-size: 20px;
  }
  .textAgain {
    font-size: 28px;
    margin-bottom: 5px;
  }
}

@media (max-width: 320px) {
  .textWin {
    font-size: 40px;
  }
  .textTimer {
    text-align: center;
    font-size: 18px;
  }
  .textAgain {
    font-size: 24px;
  }
}
