.container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 0;
  width: 100px;
  height: 100px;
  border: 1px solid #3a1010;
  outline: 1px solid #3a1010;
  background-image: url("./../../../assets/img/cellBackground.jpg");
}

.container.active {
  top: 0;
  left: 0;
  z-index: 10;
}
.container.wrong span {
  color: red;
  animation: wrong 0.1s;
}

.container.wrong {
}
.container.active span {
  color: #cf8972;
  font-size: 55px;
}

.container span {
  pointer-events: none;
  font-size: 46px;
  color: #896c62;
  text-shadow: 0px 0px 6px black;
}

.container.emptyCell {
  background: transparent;
  border: none;
}

@media (max-width: 600px) {
  .container {
    width: 81px;
    height: 81px;
  }
  .container span {
    font-size: 40px;
  }
  .container.active span {
    font-size: 50px;
  }
}

@media (max-width: 480px) {
  .container {
    width: 71px;
    height: 71px;
  }
  .container span {
    font-size: 38px;
  }
  .container.active span {
    font-size: 46px;
  }
}

@media (max-width: 400px) {
  .container {
    width: 58px;
    height: 58px;
  }
  .container span {
    font-size: 34px;
  }
  .container.active span {
    font-size: 42px;
  }
}

@media (max-width: 320px) {
  .container {
    width: 53px;
    height: 53px;
  }
  .container span {
    font-size: 26px;
  }
  .container.active span {
    font-size: 34px;
  }
}

@keyframes wrong {
  0% {
    transform: translatex(-10px);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(-10px);
  }
}
