.container {
  position: absolute;
  width: 220px;
  height: 100px;

  background: rgba(0, 0, 0, 0.6);
  color: white;
  display: flex;
  padding-bottom: 10px;
  top: 40px;
  font-size: 60px;
  border-radius: 15px;
}

.timer {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.container span {
  display: block;
}

.colon {
  position: relative;
  top: -5px;
}

@media (max-width: 600px) {
  .container {
    width: 200px;
    height: 80px;

    padding-bottom: 7px;
    font-size: 50px;
  }
}

@media (max-width: 400px) {
  .container {
    width: 150px;
    height: 60px;
    top: 30px;
    padding-bottom: 5px;
    font-size: 40px;
  }
  .colon {
    position: relative;
    top: -3px;
  }
}

@media (max-width: 320px) {
  .container {
    width: 130px;
    height: 50px;
    top: 20px;
    padding-bottom: 3px;
    font-size: 34px;
  }
  .colon {
    position: relative;
    top: -3px;
  }
}
