.container {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100vh;
  padding: 30px 0px;
  display: flex;
  justify-content: center;
}
.plateContainer {
  position: relative;
  width: 1000px;
}
.plate {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 30px;
  border: 2px solid #744c3d;
  background-image: url("../../assets/img/recordsBackground.jpg");

  padding: 80px 30px;
  overflow-y: scroll;
}

.close {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 15px;
  right: 15px;
  background: transparent;
  z-index: 100;
}

.close::before,
.close::after {
  position: absolute;
  top: 43%;

  content: "";
  width: 100%;
  height: 3px;
  display: block;
  background: black;
}
.close::before {
  transform: rotate(45deg);
}
.close::after {
  transform: rotate(-45deg);
}

@media (max-width: 1200px) {
  .plateContainer {
    width: 80%;
  }
}

@media (max-width: 800px) {
  .container {
    padding: 30px 80px;
  }
  .plateContainer {
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 767px) {
  .container {
    padding: 30px 70px;
  }
}

@media (max-width: 700px) {
  .container {
    padding: 25px 50px;
  }
}

@media (max-width: 600px) {
  .container {
    padding: 22px 35px;
  }
  .plate {
    padding: 80px 20px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 20px 28px;
  }
}

@media (max-width: 320px) {
  .container {
    padding: 15px;
  }
}
