.container {
  padding: 20px;

  height: 100%;
}

.text {
  text-align: center;
  font-size: 60px;
  margin-bottom: 90px;
}
.buttons {
  display: flex;
  justify-content: space-around;
}

@media (max-width: 600px) {
  .text {
    font-size: 50px;
    margin-bottom: 80px;
  }
}

@media (max-width: 480px) {
  .text {
    font-size: 40px;
    margin-bottom: 65px;
  }
}

@media (max-width: 400px) {
  .text {
    font-size: 34px;
    margin-bottom: 50px;
  }
}

@media (max-width: 320px) {
  .text {
    font-size: 30px;
    margin-bottom: 45px;
  }
}
