.container {
  display: flex;
  margin-bottom: 5px;
  justify-content: center;
  color: #320808;
  text-shadow: 0px 0px 1px black;
}
.container span {
  font-size: 28px;
}
.date {
  margin-right: 100px;
  font-weight: bold;
}
.time {
}

@media (max-width: 800px) {
  .container span {
    font-size: 24px;
  }
  .date {
    margin-right: 70px;
  }
}

@media (max-width: 600px) {
  .container span {
    font-size: 20px;
  }
  .date {
    margin-right: 45px;
  }
}

@media (max-width: 480px) {
  .container {
    flex-direction: column;
    margin-bottom: 20px;
  }
  .container span {
  }
  .date,
  .time {
    text-align: center;
  }
  .date {
    margin: 0;
  }
}

@media (max-width: 400px) {
  .container span {
    font-size: 18px;
  }
}

@media (max-width: 320px) {
}
