.button {
  transition: all 0.2s;
  width: 180px;
  height: 50px;
  border-radius: 15px;
  color: white;
  font-size: 36px;
  background-color: #00b300;
  border: 3px solid green;
}
.button:active {
  transform: scale(0.9);
  box-shadow: 0px 0px 10px 3px black;
}

@media (max-width: 600px) {
  .button {
    width: 160px;
    height: 40px;
    font-size: 26px;
  }
}

@media (max-width: 480px) {
  .button {
    width: 120px;
    height: 36px;
    font-size: 22px;
  }
}

@media (max-width: 400px) {
  .button {
    width: 100px;
    height: 34px;
    font-size: 18px;
  }
}

@media (max-width: 320px) {
  .button {
    width: 90px;
    height: 30px;
    font-size: 18px;
  }
}
